
import React, { Component } from 'react';
// import logo from './logo.svg';
import './App.css';
import Nav from './components/Nav'
import { Switch, Route, Link } from 'react-router-dom'
import GameFixtures from './components/GameFixtures';
import Home from './components/Home'
import ClubHistory from './components/ClubHistory'
import Players from './components/Players'
import Weather from './components/Weather'



class App extends Component {

  constructor(){
    super()
    this.state={

    }
    }
  
  render() {
    return (
      <div>
      <div className="app">
         <Nav/>  
          
     

        <Switch>

          <Route component={Home} path="/Home" exact/>
          <Route component={ GameFixtures } path="/gamefixtures"/>
          <Route component={ ClubHistory } path="/history" />
          <Route component={ Players } path="/players" />
          <Route component={Weather} path="/weather"/>
        
        </Switch>
     
          
          </div>
         
          </div>

    );
  }
}

export default App;
// user colon to narrow down to specific things...
//page details and anything after it is page 3... 
//reference to the third route in code.. this pattern is used
//when you are going to a specific item in a larger list.. 
//When it is just a followup to a noun..



