import React, {Component} from 'react';
import { Switch, Route, Link } from 'react-router-dom'
import Nav from './Nav'
import axios from 'axios'
import '../Weather.css'
import snow from './snow.png'


export default class Weather extends Component{ 
    
    constructor(){
        super();
        this.state={
            cityName: null,
            date: null,
            weather: null,
            weatherDesc: null,
            temp: null
            

        }
    }

    componentWillMount(){

        axios.get("http://api.openweathermap.org/data/2.5/weather?id=3333169&APPID=39e9ff025e40b7f2c450d11efed487fd").then((response)=>{
            
            this.setState({
                cityName: response.data.name,
                // date: response.data.list[0].dt_txt,
                weather:response.data.weather[0].description,
                temp:response.data.main.temp
            })
        })
        
        setInterval(()=> {
            axios.get("http://api.openweathermap.org/data/2.5/weather?id=3333169&APPID=39e9ff025e40b7f2c450d11efed487fd").then((response)=>{
                
                // this.setState({
                    //     cityName: response.data.city.name,
                    //     date: response.data.list[0].dt_txt,
                    //      weather:response.data.list[0].weather[0].description
                    // })
                    console.log(response);
                })
                
            }, 600000)
            
            
            
            
        }
        
        componentDidMount(){
            
            
            
            axios.get("http://api.openweathermap.org/data/2.5/forecast?id=3333169&APPID=39e9ff025e40b7f2c450d11efed487fd")
            .then((response)=>{
                if (response.data.list[0].weather[0].description ==="clear sky"){
                    this.setState({
                        background: "../clear_sky.png"
                    }) ;
                }
                else if (response.data.list[0].weather[0].description ==="few clouds"){
                    this.setState({
                        background:"../few_clouds.png"
                    });
                }else if (response.data.list[0].weather[0].description ==="scattered clouds"){
                    this.setState({
                        background:"../scattered_clouds.png"
                    });
                }else if (response.data.list[0].weather[0].description ==="broken clouds"){
                    this.setState({
                        background:"../broken_clouds.png"
                    })   
                }else if (response.data.list[0].weather[0].description ==="shower rain"){
                    this.setState({
                        background: "../shower_rain.png"
                    })
                }else if (response.data.list[0].weather[0].description ==="rain"){
                    this.setState({
                        background: "../rain.png"
                    })
                }else if (response.data.list[0].weather[0].description ==="thunderstorm"){
                    this.setState({
                        background: "../thunderstorm.png"
                    })
                }else if (response.data.list[0].weather[0].description ==="snow"){
                    this.setState({
                        background: "../snow.png"
                    })
                }else if  (response.data.list[0].weather[0].description ==="mist"){
                    this.setState({
                        background: "../mist.png"
                    })
                }
                // console.log(this.state.background);    
            }  )
            
            
        }
        
        
        
        render(){
            return (
                
                <div className= "weather">
<div>
<h1> Powered by OpenWeatherMap</h1>
<h2> {this.state.cityName} </h2>   
<h2> Current Temperature:{Math.floor(9/5 * ((this.state.temp - 273) + 32))} Deg</h2>
<h2>Description: {this.state.weather}</h2>
{/* <div><img className ="weatherImage"src ="./snow.png"/>
</div> */}
</div>

</div>
        )
    }
}



